:root {
  --color-base-50: #f8fafc;
  --color-base-100: #f1f5f9;
  --color-base-200: #e2e8f0;
  --color-base-300: #cbd5e1;
  --color-base-400: #94a3b8;
  --color-base-500: #64748b;
  --color-base-600: #475569;
  --color-base-700: #334155;
  --color-base-800: #1e293b;
  --color-base-900: #0f172a;

  --color-data-coral: #FF5733;
  --color-data-blue: #2979FF;
  --color-data-teal: #14b8a6;
  --color-data-purple: #9c27b0;
  --color-data-green: #4caf50;

  --shadow-card: inset 0 1px 0 0 rgba(241, 245, 249, 0.05), 
                 0 4px 8px 0 rgba(0, 0, 0, 0.3);
  --shadow-card-hover: inset 0 1px 0 0 rgba(241, 245, 249, 0.08),
                       0 8px 16px 0 rgba(0, 0, 0, 0.4);
  --shadow-button: inset 0 1px 0 0 rgba(241, 245, 249, 0.03),
                   0 2px 4px 0 rgba(0, 0, 0, 0.2);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.3),
               0 4px 6px -4px rgba(0, 0, 0, 0.2);

  --space-xs: 4px;
  --space-sm: 8px;
  --space-md: 16px;
  --space-lg: 24px;
  --space-xl: 32px;
  --space-2xl: 48px;

  --bg-page: #10141A;
  --bg-card: #171c23;
  --bg-elevated: #1f2937;
  --border-default: #2d3748;
  --text-primary: #EAEAEA;
  --text-secondary: #9AA8BD;
  --text-tertiary: #a0aec0;
}

[data-theme="light"] {
  --color-base-900: #ffffff;
  --color-base-800: #f8fafc;
  --color-base-700: #f1f5f9;
  --color-base-600: #e2e8f0;
  --color-base-500: #cbd5e1;
  --color-base-400: #94a3b8;
  --color-base-300: #64748b;
  --color-base-200: #475569;
  --color-base-100: #334155;
  --color-base-50: #1e293b;

  --bg-page: #ffffff;
  --bg-card: #f8fafc;
  --bg-elevated: #f1f5f9;
  --border-default: #e2e8f0;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --text-tertiary: #94a3b8;

  --shadow-card: inset 0 1px 0 0 rgba(255, 255, 255, 0.8), 
                 0 4px 8px 0 rgba(0, 0, 0, 0.05);
  --shadow-card-hover: inset 0 1px 0 0 rgba(255, 255, 255, 0.9),
                       0 8px 16px 0 rgba(0, 0, 0, 0.08);
  --shadow-button: inset 0 1px 0 0 rgba(255, 255, 255, 0.5),
                   0 2px 4px 0 rgba(0, 0, 0, 0.04);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.08),
               0 4px 6px -4px rgba(0, 0, 0, 0.04);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg-page);
  color: var(--text-primary);
}

.heading-1 { font-size: 28px; font-weight: 700; line-height: 1.2; }
.heading-2 { font-size: 24px; font-weight: 600; line-height: 1.3; }
.body-large { font-size: 16px; font-weight: 400; line-height: 1.5; }
.body-base { font-size: 14px; font-weight: 400; line-height: 1.5; }
.label { font-size: 12px; font-weight: 500; letter-spacing: 0.5px; text-transform: uppercase; }

/* Main App Container */
.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: var(--bg-page);
  color: var(--text-primary);
}

/* Header */
.app-header {
  background: var(--bg-card);
  border-bottom: 1px solid var(--border-default);
  padding: var(--space-lg) var(--space-lg);
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: var(--shadow-card);
  z-index: 10;
  position: relative;
}

.app-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(241, 245, 249, 0.1), 
    transparent
  );
}

.app-header h1 {
  font-size: 32px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  letter-spacing: 0.05em;
}

.header-info {
  display: flex;
  align-items: center;
  gap: var(--space-md);
}

.data-count {
  background: rgba(148, 163, 184, 0.1);
  color: var(--color-base-400);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 14px;
  border: 1px solid rgba(148, 163, 184, 0.2);
}

/* Body Layout */
.app-body {
  display: flex;
  height: auto;
}

/* Control Panel - Left Sidebar */
.control-panel {
  width: 280px;
  background: var(--bg-card);
  border-right: 1px solid var(--border-default);
  padding: var(--space-lg);
  overflow-y: auto;
  box-shadow: inset -1px 0 0 0 var(--border-default);
}

.panel-section {
  margin-bottom: var(--space-xl);
}

.panel-section h3 {
  font-size: 16px;
  font-weight: 600;
  color: var(--color-base-200);
  margin-bottom: var(--space-md);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 12px;
}

.control-group {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.control-slider {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 6px;
  border-radius: 3px;
  background: var(--border-default);
  outline: none;
  cursor: pointer;
  transition: background 0.2s ease;
}

.control-slider:focus-visible {
  outline: 2px solid var(--color-base-400);
  outline-offset: 2px;
}

.control-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--color-base-400);
  cursor: pointer;
  border: 2px solid var(--color-base-100);
  box-shadow: var(--shadow-button);
  transition: all 0.2s ease;
}

.control-slider::-webkit-slider-thumb:hover {
  background: var(--color-base-300);
  transform: scale(1.1);
}

.control-slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--color-base-400);
  cursor: pointer;
  border: 2px solid var(--color-base-100);
  box-shadow: var(--shadow-button);
}

.control-value {
  color: var(--color-base-400);
  font-weight: 600;
  font-size: 14px;
  text-align: center;
  background: rgba(148, 163, 184, 0.1);
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid rgba(148, 163, 184, 0.2);
}

.price-range-display {
  color: var(--color-base-200);
  font-size: 14px;
  text-align: center;
  background: rgba(226, 232, 240, 0.1);
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid rgba(226, 232, 240, 0.2);
  margin-top: 8px;
}

/* Dual-handle range slider (CSS-only) */
.range-slider {
  position: relative;
  height: 24px;
}

.range-input {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 24px; /* match slider height so thumbs center correctly */
  background: none;
  pointer-events: none; /* allow highlight div to be clickable-through */
}

.range-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  pointer-events: auto;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--color-base-400);
  border: 2px solid var(--color-base-100);
}

.range-input::-moz-range-thumb {
  pointer-events: auto;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--color-base-400);
  border: 2px solid var(--color-base-100);
}

.range-track {
  position: absolute;
  top: calc(50% - 3px);
  left: 0;
  right: 0;
  height: 6px;
  border-radius: 3px;
  background: var(--border-default);
}

.range-highlight {
  position: absolute;
  top: calc(50% - 3px);
  height: 6px;
  border-radius: 3px;
  background: var(--color-base-400);
}

/* Toggle Button */
.toggle-button {
  width: 100%;
  background: var(--border-default);
  border: 1px solid var(--color-base-600);
  color: var(--color-base-200);
  padding: 12px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  text-align: left;
  box-shadow: var(--shadow-button);
}

.toggle-button:hover {
  background: var(--color-base-600);
  border-color: var(--color-base-500);
  transform: translateY(-1px);
  box-shadow: var(--shadow-lg);
}

.toggle-button:focus-visible {
  outline: 2px solid var(--color-base-400);
  outline-offset: 2px;
}

.toggle-button.active {
  background: rgba(148, 163, 184, 0.2);
  border-color: var(--color-base-400);
  color: var(--color-base-400);
}

/* Horizontal Legend - Overlay on Map */
.horizontal-legend {
  position: absolute;
  bottom: 16px;
  left: 16px;
  background: rgba(23, 28, 35, 0.9);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-default);
  border-radius: 8px;
  padding: 12px 16px;
  z-index: 5;
  min-width: 300px;
  box-shadow: var(--shadow-card);
}

.horizontal-legend .legend-label {
  display: block;
  font-size: 11px;
  color: var(--color-base-200);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-weight: 600;
  margin-bottom: 8px;
  text-align: center;
}

.horizontal-legend-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.horizontal-gradient-bar {
  flex: 1;
  height: 16px;
  border-radius: 8px;
  border: 1px solid var(--color-base-600);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2);
}

.horizontal-legend .legend-value {
  font-size: 11px;
  color: var(--text-tertiary);
  font-weight: 600;
  min-width: 50px;
  text-align: center;
}

/* Map Wrapper with Touch Surface */
.map-wrapper {
  flex: 1;
  padding: var(--space-md);
  background: var(--bg-page);
}

/* Map Container */
.map-container {
  width: 100%;
  height: 600px;
  position: relative;
  background: var(--bg-page);
  overflow: hidden;
  border-radius: 8px;
  border: 1px solid var(--border-default);
}

/* Map Info Overlay */
.map-info {
  position: absolute;
  top: 16px;
  right: 16px;
  background: rgba(23, 28, 35, 0.9);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-default);
  border-radius: 8px;
  padding: 12px 16px;
  z-index: 5;
  box-shadow: var(--shadow-card);
}

.info-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.info-label {
  color: var(--text-tertiary);
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.info-value {
  color: var(--color-base-400);
  font-weight: 600;
  font-size: 18px;
}

/* Analytics Panel - Right Side */
.analytics-panel {
  width: 520px;
  background: var(--bg-card);
  border-left: 1px solid var(--border-default);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.analytics-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px;
  border-bottom: 1px solid var(--border-default);
  background: var(--bg-card);
}

.analytics-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--color-base-200);
  margin: 0;
}

.close-button {
  background: none;
  border: none;
  color: var(--text-tertiary);
  font-size: 24px;
  cursor: pointer;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.close-button:hover {
  background: var(--border-default);
  color: var(--color-base-200);
}

.close-button:focus-visible {
  outline: 2px solid var(--color-base-400);
  outline-offset: 2px;
}

.analytics-content {
  flex: 1;
  padding: 24px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* Chart Styling Updates for Dark Theme */
.bar-chart-container,
.scatter-plot-container,
.amenity-chart-container,
.donut-chart-container,
.bedrooms-chart-container,
.line-chart-container {
  background: var(--bg-card);
  border: 1px solid var(--border-default);
  border-radius: 8px;
  padding: 20px;
  box-shadow: var(--shadow-card);
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s ease;
  position: relative;
}

.bar-chart-container::before,
.scatter-plot-container::before,
.amenity-chart-container::before,
.donut-chart-container::before,
.bedrooms-chart-container::before,
.line-chart-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(241, 245, 249, 0.1), 
    transparent
  );
}

.bar-chart-container:hover,
.scatter-plot-container:hover,
.amenity-chart-container:hover,
.donut-chart-container:hover,
.bedrooms-chart-container:hover,
.line-chart-container:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-card-hover);
}

.bar-chart-container h3,
.scatter-plot-container h3,
.amenity-chart-container h3,
.donut-chart-container h3,
.bedrooms-chart-container h3,
.line-chart-container h3 {
  color: var(--text-primary);
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 16px;
}

.bar-chart-scroll {
  background: var(--bg-page);
  border: 1px solid var(--border-default);
  border-radius: 6px;
  max-height: 400px;
  overflow-y: auto;
}

.bar-chart-scroll::-webkit-scrollbar {
  width: 6px;
}

.bar-chart-scroll::-webkit-scrollbar-track {
  background: var(--border-default);
  border-radius: 3px;
}

.bar-chart-scroll::-webkit-scrollbar-thumb {
  background: var(--color-base-400);
  border-radius: 3px;
}

.bar-chart-scroll::-webkit-scrollbar-thumb:hover {
  background: var(--color-base-300);
}

/* Generic chart scroll wrapper to align card heights */
.chart-scroll {
  background: var(--bg-page);
  border: 1px solid var(--border-default);
  border-radius: 6px;
  max-height: 400px;
  overflow-y: auto;
}

/* Charts Section - Below Map */
.charts-section {
  background: var(--bg-page);
  padding: var(--space-xl);
  border-top: 1px solid var(--border-default);
}

/* Section Headers */
.section-header {
  background: var(--bg-page);
  padding: var(--space-xl) var(--space-xl) var(--space-md) var(--space-xl);
  border-bottom: 1px solid var(--border-default);
  position: relative; /* Added to position the pseudo-element */
  padding-bottom: calc(var(--space-md) + 12px); /* Added space for the gradient line */
  margin-top: 70px; /* Increased vertical spacing above section headers */
}

.section-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, 
    var(--color-base-400) 0px, 
    var(--bg-card) 500px,
    transparent
  );
}

.section-header h2 {
  color: var(--text-primary);
  font-size: 24px;
  font-weight: 600;
  margin: 0;
  letter-spacing: 0.05em;
  text-align: left;
}

.charts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  gap: 32px;
  max-width: 1400px;
  margin: 0 auto;
  align-items: stretch;
}

/* Normalize chart card heights */
.bar-chart-container,
.donut-chart-container,
.bedrooms-chart-container,
.amenity-chart-container,
.scatter-plot-container,
.line-chart-container {
  min-height: 360px;
  display: flex;
  flex-direction: column;
}

.donut-chart-container svg,
.bedrooms-chart-container svg,
.amenity-chart-container svg,
.scatter-plot-container svg,
.bar-chart-container svg,
.line-chart-container svg {
  flex: 1;
}

/* Mini legends shared by charts */
.mini-legend {
  margin-top: 10px;
  font-size: 11px;
  color: var(--text-tertiary);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.mini-gradient {
  width: 120px;
  height: 8px;
  border-radius: 4px;
  border: 1px solid var(--border-default);
  background: linear-gradient(90deg, #440154, #31688e, #35b779, #fde725);
}

.legend-square {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 2px;
}

/* Tooltip Styling */
.tooltip {
  position: absolute;
  pointer-events: none;
  background: rgba(23, 28, 35, 0.95) !important;
  backdrop-filter: blur(12px) !important;
  border: 1px solid var(--border-default) !important;
  border-radius: 8px !important;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4) !important;
  color: #ffffff !important;
  font-size: 13px !important;
  z-index: 1000 !important;
  min-width: 200px;
}

.tooltip-header {
  padding: 12px 16px 8px 16px;
  border-bottom: 1px solid var(--border-default);
  color: var(--color-base-400);
  font-weight: 600;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tooltip-body {
  padding: 12px 16px;
}

.tooltip-body div {
  margin: 6px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tooltip-id {
  font-size: 11px;
  color: var(--text-tertiary);
  font-family: monospace;
  border-top: 1px solid var(--border-default);
  padding-top: 8px !important;
  margin-top: 8px !important;
}

/* Loading States */
.loading {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--bg-page);
  color: var(--text-primary);
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  margin-bottom: 24px;
  border: 3px solid var(--border-default);
  border-top: 3px solid var(--color-base-400);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--color-base-200);
}

.loading p {
  font-size: 16px;
  color: var(--text-tertiary);
  max-width: 400px;
  line-height: 1.5;
}

/* Error States */
.error {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--bg-page);
  color: var(--text-primary);
  text-align: center;
}

.error h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 12px;
  color: #f56565;
}

.error p {
  font-size: 16px;
  color: var(--text-tertiary);
  max-width: 500px;
  line-height: 1.5;
  margin: 8px 0;
}

/* BAN (Big Ass Number) Components */
.bans-section {
  background: var(--bg-page);
  border-bottom: 1px solid var(--border-default);
  padding: var(--space-lg) 20px;
}

.bans-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--space-md);
  max-width: 900px;
  margin: 0 auto;
}

.ban-container {
  background: var(--bg-card);
  border: 1px solid var(--border-default);
  border-radius: 8px;
  padding: 20px 16px;
  text-align: center;
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s ease;
  min-width: 0;
  min-height: 60px;
  overflow: hidden;
  box-shadow: var(--shadow-card);
  position: relative;
}

.ban-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(241, 245, 249, 0.1), 
    transparent
  );
}

.ban-container:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-card-hover);
}

.ban-value {
  font-size: 28px;
  font-weight: 700;
  color: var(--color-data-coral);
  margin-bottom: 8px;
  line-height: 1;
  overflow-wrap: break-word;
  word-break: break-word;
}

.ban-label {
  font-size: 11px;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-weight: 300;
  overflow-wrap: break-word;
  word-break: break-word;
}

/* ============================================
   RESPONSIVE DESIGN - Mobile First Approach
   ============================================ */

/* Desktop Small: 1024px - 1366px */
@media (max-width: 1366px) {
  /* BANs Grid: Keep 3 columns but slightly smaller */
  .bans-grid {
    gap: 14px;
    max-width: 800px;
  }
  
  .ban-container {
