/* Custom scrollbars for all elements */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-page);
}

::-webkit-scrollbar-thumb {
  background: var(--border-default);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-base-600);
}

::-webkit-scrollbar-corner {
  background: var(--bg-page);
}

/* ============================================
   SIDEBAR DRAWER (for small screens)
   ============================================ */

/* Sidebar toggle button */
.sidebar-toggle {
  position: fixed;
  top: 80px;
  left: 16px;
  z-index: 999;
  background: var(--bg-card);
  border: 1px solid var(--border-default);
  padding: 10px 16px;
  border-radius: 6px;
  color: var(--text-primary);
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  display: none;
  align-items: center;
  gap: 8px;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: var(--shadow-card);
}

.sidebar-toggle:hover {
  background: var(--bg-elevated);
  border-color: var(--color-base-400);
  transform: translateY(-1px);
  box-shadow: var(--shadow-card-hover);
}

.sidebar-toggle:active {
  transform: translateY(0);
}

.sidebar-toggle:focus-visible {
  outline: 2px solid var(--color-base-400);
  outline-offset: 2px;
}

/* Show toggle button on small screens */
@media (max-width: 1024px) {
  .sidebar-toggle {
    display: flex;
  }
}

/* Overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;
  animation: fadeIn 0.2s ease-out;
  backdrop-filter: blur(2px);
}

/* Drawer */
.sidebar-drawer {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 320px;
  max-width: 85vw;
  background: var(--bg-card);
  padding: var(--space-md);
  overflow-y: auto;
  animation: slideInLeft 0.3s ease-out;
  box-shadow: 4px 0 20px rgba(0, 0, 0, 0.5);
  border-right: 1px solid var(--border-default);
  z-index: 1001;
}

/* Control panel inside drawer */
.sidebar-drawer .control-panel {
  width: 100%;
  padding: 0;
  border: none;
  box-shadow: none;
  background: transparent;
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
  height: auto;
  overflow-x: hidden;
  overflow-y: auto;
}

/* Close button in drawer */
.sidebar-drawer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-md);
  padding-bottom: 12px;
  border-bottom: 1px solid var(--border-default);
}

.sidebar-drawer-title {
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
}

.sidebar-close {
  background: transparent;
  border: none;
  color: var(--text-secondary);
  font-size: 24px;
  cursor: pointer;
  padding: 4px 8px;
  line-height: 1;
  transition: color 0.2s ease;
}

.sidebar-close:hover {
  color: var(--text-primary);
}

.sidebar-close:focus-visible {
  outline: 2px solid var(--color-base-400);
  outline-offset: 2px;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}